body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.title {
  font-size: 3em;
  font-weight: bolder;
}

.sub-title {
  margin-top: 15pt;
}

.controls {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 20pt 0;
}

.image {
  margin: 20pt 0;
}

.image-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.content-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.selected-container {
  -webkit-align-self: start;
          align-self: start;
  margin-bottom: 20pt;
}

.rejected-container {
  -webkit-align-self: start;
          align-self: start;
}

.text-center {
  text-align: center;
}

.add {
  cursor: pointer;
}

.remove {
  cursor: pointer;
}

.links-title {
  font-size: 1.25 em;
  font-weight: 500;
}

